/*=================fonts=================*/
* {
  margin: 0;
  padding: 0;
  text-decoration-skip-ink: none; }
  * li {
    list-style: none; }

._container {
  max-width: 1180px;
  padding: 0 20px;
  margin: auto; }

.rub::after {
  content: "P";
  line-height: 5px;
  width: 0.4em;
  border-bottom: 1px solid #000;
  display: inline-block; }

.header {
  background: #ffffff url("../img/header-nav.jpg") top no-repeat; }
  .header .top-header__content {
    display: flex;
    align-items: center;
    height: 90px; }
  .header .top-header__logo {
    position: relative;
    z-index: 5;
    display: inline-block; }
  .header .top-header__column {
    flex: 0 1 50%; }
    .header .top-header__column_logo {
      flex: 0 0 190px;
      text-align: center;
      padding-top: 25px; }
    .header .top-header__column .menu__list {
      display: grid;
      grid-template-columns: 110px 130px 180px; }
      .header .top-header__column .menu__list li {
        margin: 1px 0; }
    .header .top-header__column .menu__link {
      font-family: 'Play-Bold';
      white-space: nowrap;
      font-size: 14px;
      min-height: 27px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      color: #ffffff;
      padding: 2px 22px; }
    .header .top-header__column .icon-menu {
      display: none; }
    .header .top-header__column .contacts-header {
      display: flex;
      justify-content: center;
      position: relative;
      z-index: 5; }
      .header .top-header__column .contacts-header__column {
        padding: 0 30px;
        border-right: 1px solid #ffffff; }
        .header .top-header__column .contacts-header__column:last-child {
          border: none; }
      .header .top-header__column .contacts-header__item_icon {
        background: url("../img/icons/phone.png") left no-repeat;
        padding: 0px 0px 0px 37px; }
      .header .top-header__column .contacts-header__label {
        font-family: 'Roboto-Light';
        color: #ffffff;
        font-size: 12px;
        margin-bottom: 5px; }
      .header .top-header__column .contacts-header__phone {
        font-family: 'Roboto-Black';
        color: #ffffff;
        font-size: 18px;
        text-decoration: none;
        white-space: nowrap; }
  .header .bottom-header__content {
    display: flex;
    height: 63px;
    align-items: center;
    justify-content: space-between;
    padding-left: 35px; }
  .header .bottom-header__column {
    flex: 0 1 calc(50% - 95px); }
  .header .bottom-header .actions-header {
    display: flex;
    align-items: center; }
    .header .bottom-header .actions-header li {
      font-family: 'Roboto-Light';
      margin: 0px 54px 0px 0px; }
      .header .bottom-header .actions-header li:last-child {
        margin: 0px; }
    .header .bottom-header .actions-header__item {
      padding-left: 36px;
      display: inline-block;
      line-height: 24px;
      text-decoration: none;
      color: #999999; }
      .header .bottom-header .actions-header__item span {
        font-size: 14px;
        border-bottom: 2px dotted #999999; }
        .header .bottom-header .actions-header__item span:hover {
          color: #1ab9ce;
          border-bottom: 2px dotted #1ab9ce; }
      .header .bottom-header .actions-header__item_login {
        background: url("../img/icons/login.png") left no-repeat; }
      .header .bottom-header .actions-header__item_reg {
        background: url("../img/icons/registration.png") left no-repeat; }
      .header .bottom-header .actions-header__item_email {
        background: url("../img/icons/mail.png") left no-repeat; }
        .header .bottom-header .actions-header__item_email span {
          border-bottom: 1px solid; }
          .header .bottom-header .actions-header__item_email span:hover {
            border-bottom: 1px solid; }
  .header .bottom-header .info-header {
    display: flex;
    justify-content: center;
    align-items: center; }
    .header .bottom-header .info-header__column {
      font-family: 'Roboto-Light';
      padding: 0px 35px 0px 0px; }
    .header .bottom-header .info-header span {
      font-size: 14px; }
    .header .bottom-header .info-header__callback {
      padding-left: 36px;
      background: url("../img/icons/callback.png") left no-repeat;
      display: inline-block;
      color: #1ab9ce;
      text-decoration: none;
      white-space: nowrap; }
      .header .bottom-header .info-header__callback span {
        border-bottom: 2px dotted #1ab9ce; }
    .header .bottom-header .info-header__schedule {
      padding-left: 36px;
      color: #999999;
      background: url("../img/icons/schedule.png") left no-repeat;
      line-height: 18px; }
      .header .bottom-header .info-header__schedule span {
        font-family: 'Roboto-Black'; }
      .header .bottom-header .info-header__schedule p {
        font-size: 14px; }
    .header .bottom-header .info-header__cart {
      width: 43px;
      height: 43px;
      display: inline-block;
      background: url("../img/icons/cart-blue.png") 0 0 no-repeat;
      position: relative; }
      .header .bottom-header .info-header__cart span {
        padding: 0 11px;
        border-radius: 20px;
        line-height: 25px;
        background: #f68038;
        position: absolute;
        right: 0;
        transform: translate(60%, 0px);
        color: #ffffff;
        font-size: 18px;
        font-family: 'Roboto-Bold'; }

.page {
  flex: 1 1 auto; }
  .page__container {
    display: flex; }
  .page__side {
    flex: 0 0 260px; }
  .page__content {
    flex: 1 1 auto;
    padding: 0px 0px 0px 30px; }

.menu-page {
  position: relative;
  background: url("../img/aside-nav.jpg") top no-repeat; }
  .menu-page__header {
    padding: 20px;
    display: flex;
    align-items: center; }
  .menu-page__title {
    font-family: 'UniSansHeavyCAPS';
    color: #ffffff;
    font-size: 30px; }
  .menu-page__burger {
    flex: 0 0 30px;
    height: 18px;
    background: #068292;
    position: relative;
    cursor: pointer;
    z-index: 5;
    padding: 10px 6px; }
    .menu-page__burger span {
      transition: all 0.3s ease 0s;
      top: calc(50% - 1px);
      left: 0px;
      position: absolute;
      width: 100%;
      height: 2px;
      background: #ffffff; }
      .menu-page__burger span:first-child {
        top: 0px; }
      .menu-page__burger span:last-child {
        top: auto;
        bottom: 0px; }
    .menu-page__burger._active span {
      transform: scale(0); }
      .menu-page__burger._active span:first-child {
        transform: rotate(-45deg);
        top: calc(50% - 1px); }
      .menu-page__burger._active span:last-child {
        transform: rotate(45deg);
        top: calc(50% - 1px); }
  .menu-page__lines {
    height: 16px;
    position: relative; }
  .menu-page__list li > {
    border-bottom: 1px solid #1aa7b9; }
  .menu-page__parent .menu-page__link::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 10px;
    width: 14px;
    height: 14px;
    transform: translate(0px, -50%);
    background: url("../img/icons/arrow-nav.png"); }
  .menu-page__parent._active .submenu-page__item {
    z-index: 2;
    transform: translate(0px, 0px); }
  .menu-page__parent._active .menu-page__link {
    background: #098494; }
  .menu-page__link {
    font-family: 'Roboto-Light';
    color: #ffffff;
    font-size: 12px;
    text-decoration: none;
    display: block;
    padding: 12px 20px;
    position: relative; }
  .menu-page__submenu {
    width: 100%;
    position: absolute;
    top: 0;
    height: 100%;
    left: 100%;
    overflow: hidden; }

.submenu-page__menu {
  border-bottom: 1px solid #dedede;
  padding: 20px 0; }

.submenu-page__link {
  font-family: 'Roboto-Light';
  font-size: 12px;
  text-decoration: none;
  color: #1ab9ce;
  padding: 10px 40px;
  display: block; }

.submenu-page__product {
  padding: 0 15px; }

.submenu-page__item {
  border: 2px solid #098494;
  background: #ffffff;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  transition: all 0.3s ease 0s;
  transform: translate(-100%, 0px); }

.item-product {
  padding: 15px 0;
  position: relative; }
  .item-product__body {
    padding: 0 10px; }
  .item-product__footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center; }
  .item-product__labels {
    position: absolute;
    left: 15px;
    top: 15px; }
  .item-product__label {
    margin: 0px 0px 5px 0px;
    font-family: 'Roboto-Black';
    font-size: 14px;
    color: #ffffff;
    background: #fd8424;
    text-transform: uppercase;
    padding: 0px 15px;
    height: 21px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center; }
  .item-product__image {
    height: 195px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0; }
    .item-product__image img {
      max-width: 100%;
      max-height: 175px; }
  .item-product__title {
    font-family: 'Roboto-Light';
    font-size: 16px;
    color: #36949f;
    text-decoration: none;
    display: inline-block;
    margin: 0px 0px 10px 0px; }
  .item-product__old-price {
    flex: 1 1 100%;
    font-family: 'Roboto-Light';
    font-size: 18px;
    color: #999999;
    text-decoration: line-through;
    text-align: right; }
    .item-product__old-price::after {
      font-size: 14px; }
  .item-product__add {
    width: 40px;
    height: 40px;
    background: url("../img/icons/cart-grey.png") 0 0 no-repeat; }
  .item-product__price {
    font-family: 'Roboto-Black';
    font-size: 24px;
    color: #333333; }
    .item-product__price::after {
      font-size: 20px; }
